.App {
  text-align: center;
  background-image: url("https://i.ibb.co/gwpS2PJ/photo-2024-06-30-17-13-00.jpg");
  background-size: cover;
  background-position: center;
  width: 100vw;
  height: 100vh;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


button {
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
}

.App-bottom-nav {
  position: fixed;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: space-around;
  padding: 10px 0;
}

.App-bottom-nav img {
  width: 40px;
  height: 40px;
  cursor: pointer;
}

.App-bottom-nav img:hover {
  opacity: 0.7;
}

@media (max-width: 768px) {
  .App-bottom-nav img {
    width: 30px;
    height: 30px;
  }
}

#main-screen, #tasks-screen, #friends-screen {
  display: none;
}

#main-screen.active, #tasks-screen.active, #friends-screen.active {
  display: block;
}

.claim-button, .start-button {
  margin-top: 20px;
}

.timer-wrapper {
  margin-top: 20px;
}

#hours, #minutes {
  font-size: 1.5rem;
  font-weight: bold;
}
